<template>
<div>
  <div v-if="!loading && track">
    <md-card class="card">
      <md-card-media>
        <md-image :md-src="track.thumbnail_url"></md-image>

        <md-ink-ripple></md-ink-ripple>
      </md-card-media>

      <md-card-header>
        <div class="md-title">{{ track.title }}</div>
        <div class="md-subhead">{{ track.author_name }}</div>
      </md-card-header>

      <md-card-content>
        <md-layout class="time">
          <md-layout class="md-caption">{{currentTime}}</md-layout>
          <md-layout md-flex-offset="0" md-align="end" class="md-caption">{{duration}}</md-layout>
        </md-layout>
        <md-progress :md-progress="relativeTime"></md-progress>
      </md-card-content>

      <md-card-actions>
        <md-button class="md-icon-button" @click="player.play()">
          <md-icon>play_arrow</md-icon>
        </md-button>

        <md-button class="md-icon-button" @click="player.pause()">
          <md-icon>pause</md-icon>
        </md-button>


        <md-button class="md-icon-button" @click="player.currentTime-=10">
          <md-icon>fast_rewind</md-icon>
        </md-button>

        <md-button class="md-icon-button" @click="player.currentTime+=10">
          <md-icon>fast_forward</md-icon>
        </md-button>

      </md-card-actions>
    </md-card>
  </div>
  <div v-else-if="track">
    <md-layout md-align="center" md-vertical-align="center">
        <md-spinner :md-size="100" :md-stroke="2" md-indeterminate md-align="center"></md-spinner>
    </md-layout>
  </div>
  <div v-else>
    <md-layout md-align="center" md-vertical-align="center">
        <md-whiteframe class="frame">Nothing to play</md-whiteframe>
    </md-layout>
  </div>

  <md-snackbar md-position="bottom center" ref="snackbar" md-duration="10">
    <span>Connection timeout. Showing limited messages.</span>
    <md-button class="md-accent" md-theme="default" @click="$refs.snackbar.close()">Close</md-button>
  </md-snackbar>
</div>

</template>

<script>

export default {
  data () {
    return {
      cors: "https://cors-anywhere.herokuapp.com/",
      track: false,
      loading: false,
      player: false,
      currentTime: "0:00",
      duration: "0:00",
      relativeTime: 0,
      qualities: [
      "1080p/1920x1080,video/mp4", "1080p/1920x1080,video/webm", "720p/1280x720,video/mp4", "720p/1280x720,video/webm", "480p/854x480,video/mp4", "480p/854x480,video/webm", "360p/640x360,video/mp4", "360p/640x360,video/webm", "240p/426x240,video/mp4", "240p/426x240,video/webm", "144p/256x144,video/mp4", "144p/256x144,video/webm", "audio,audio/mp4", "audio,audio/webm", "audio,audio/webm", "audio,audio/webm", "audio,audio/webm"
      ]
    }
  },
  props: {
    id: String
  },

  methods:{
    getVideoInfo(id, callback) {
      this.$http.get('https://noembed.com/embed?url=https://www.youtube.com/watch?v='+this.id).then(data => {
        callback(data.body);
      }, reason => {
        
      });
    },
    queryStringMap(data) {
      let result = {};
      data.split('&').forEach((entry) => {
        result[
            decodeURIComponent(entry.substring(0, entry.indexOf('=')))] =
            decodeURIComponent(entry.substring(entry.indexOf('=') + 1));
      });
      return result;
    },
    listOfQueryStringMaps(data) {
      let result = [];
      data.split(',').forEach((entry) => {
        result.push(this.queryStringMap(entry));
      });
      return result;
    },

    getVideoSources(id, callback, quality=3){
      this.$http.get(this.cors+'http://www.youtube.com/get_video_info?video_id='+id+
        '&el=vevo&el=embedded&asv=3&sts=15902').then(data => {

        data = this.queryStringMap(data.bodyText);
        if (typeof data['adaptive_fmts'] == 'string') {
          data['adaptive_fmts'] = this.listOfQueryStringMaps(data['adaptive_fmts']);
        }
        callback(data.adaptive_fmts.reverse()[quality].url);

      }, reason => {
        
      });
    },
    getCurrentTime(time){
      let minutes = Math.floor(time / 60);
      let seconds = Math.floor(time - minutes * 60);
      return minutes+':'+(seconds<10 ? '0' : '')+seconds;
    },
    initTrack(){
      if(this.player){
        this.player.src = "";
        this.loading = true;
      }

      let self = this;
      if(this.id!=="false")
      this.getVideoInfo(this.id, data=>{

        if(data !== undefined && data.thumbnail_url !== undefined){
          self.track = data;
          
          self.getVideoSources(self.id, url=>{
            self.player = new Audio(url);
            self.player.autoplay = true;

            self.player.ontimeupdate = function(){
              self.currentTime = self.getCurrentTime(self.player.currentTime);
              self.relativeTime = 100/self.player.duration*self.player.currentTime;
            }

            console.log(url);

            self.player.oncanplay = function(){
              self.loading = false;
              self.duration = self.getCurrentTime(self.player.duration);
            }

          });

        }

      });
    }

  },

  created(){
    this.initTrack()
  },
  beforeDestroy(){
    this.player.pause();
    this.player.src = "";
  },
  watch:{
    'id': 'initTrack'
  }
}
</script>

<style lang="scss" scoped>
@import "./src/styles/settings.scss";

.card{
  margin: 20px;
}

.frame{
  margin: 20px;
  padding: 20px;
}
</style>


