<template>
<div>
  <md-toolbar>
    <h2 class="md-title" style="flex: 1">PlayYoutubeAudio</h2>

    <md-button class="md-icon-button" @click="openDialog('openurl')">
      <md-icon>queue</md-icon>
    </md-button>
  </md-toolbar>


	
	<md-dialog ref="openurl">
	  <md-dialog-title>Type video URL here</md-dialog-title>

	  <md-dialog-content>
	      <md-input-container>
	        <label>URL</label>
	        <md-input v-model="url"></md-input>
	      </md-input-container>
	  </md-dialog-content>

	  <md-dialog-actions>
	    <md-button class="md-primary" @click="closeDialog('openurl')">Cancel</md-button>
	    <md-button class="md-primary" @click="play()">Play</md-button>
	  </md-dialog-actions>
	</md-dialog>
</div>
</template>

<script>

export default {
	data () {
    return {
    	url: ""
    }
  },
  methods: {
    openDialog(ref){
      this.$refs[ref].open();
    },
    closeDialog(ref){
      this.$refs[ref].close();
    },
    youtube_parser(url){
        var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
        var match = url.match(regExp);
        return (match&&match[7].length==11)? match[7] : false;
    },
    play(){
      let id = this.youtube_parser(this.url)
    	if(id) this.$emit('trackadded', id);
    	this.closeDialog('openurl');
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./src/styles/settings.scss";


</style>


