<template>
  <div>
    
    <!-- <router-view></router-view> -->
    <toolbar @trackadded="play"></toolbar>
    <card :id="id"></card>

  </div>
</template>

<script>
import Toolbar from './components/Toolbar.vue';
import Card from './components/Card.vue';
// import Component from './components/Component.vue';
// import { EventBus } from './event-bus.js';

export default {
  components:{
    'toolbar': Toolbar,
    'card': Card
  },
  data () {
    return {
      id: "false"
    }
  },
  methods:{
    play(url){
      this.id = url;
    }
  }
}
</script>

<style lang="scss">

</style>



